.nav-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 20px;
  transition: all 0.5s ease-out;
  z-index: 1;
}
.nav-bar .logo {
  color: #141720;
  font-size: 20px;
  font-weight: 400;
  display: inline-block;
}
.nav-bar__links {
  display: flex;
}
@media screen and (max-width: 768px) {
  .nav-bar__links {
    position: absolute;
    top: 0;
    right: 0;
    background-color: #c71222;
    height: 100vh;
    width: 320px;
    transform: translateX(100%);
    flex-direction: column;
    align-items: center;
    padding-top: 75px;
    transition: all 0.5s ease-out;
  }
}
.nav-bar__items {
  margin: 0 25px;
}
.nav-bar__items:last-child {
  margin-right: 0;
}
.nav-bar__items.active a {
  color: #c71222;
}
.nav-bar__items a {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.5px;
  color: #5d5f65;
}
.nav-bar__items a:hover {
  color: #c71222;
}
@media screen and (max-width: 768px) {
  .nav-bar__items {
    margin: 20px 22px;
  }
  .nav-bar__items a {
    color: #141720;
  }
  .nav-bar__items a:hover {
    color: #141720;
  }
  .nav-bar__items.active a {
    color: #141720;
  }
}
.nav-bar .burger {
  display: none;
}
@media screen and (max-width: 768px) {
  .nav-bar .burger {
    display: block;
    cursor: pointer;
    caret-color: transparent;
  }
  .nav-bar .burger .line1 {
    display: block;
    transition: all 0.5s ease-out;
  }
  .nav-bar .burger .line2 {
    transform: rotate(0deg);
    transition: all 0.5s ease-out;
  }
  .nav-bar .burger .line3 {
    transform: rotate(0deg);
    transition: all 0.5s ease-out;
  }
}
.nav-bar.add-bg {
  background-color: #141720;
}
.nav-bar.add-bg .logo {
  color: #ffffff;
}
.nav-bar.add-bg .nav-bar__items a {
  color: #ffffff;
}
.nav-bar.add-bg .nav-bar__items a:hover {
  color: #c71222;
}
.nav-bar.add-bg .nav-bar__items.active a {
  color: #c71222;
}
@media screen and (max-width: 768px) {
  .nav-bar.add-bg [class*=line] {
    background-color: #ffffff;
  }
  .nav-bar.add-bg .nav-bar__items.active a {
    color: #141720;
  }
  .nav-bar.add-bg .nav-bar__items a:hover {
    color: #141720;
  }
}
@media screen and (max-width: 768px) {
  .nav-bar.show-on-mobile .nav-bar__links {
    transform: translate(0);
  }
  .nav-bar.show-on-mobile .nav-bar__links .nav-bar__items.active a {
    color: #141720;
  }
  .nav-bar.show-on-mobile .nav-bar__links .nav-bar__items a {
    color: #ffffff;
  }
  .nav-bar.show-on-mobile .burger .line1 {
    visibility: hidden;
    transition: all 0.5s ease-out;
  }
  .nav-bar.show-on-mobile .burger .line2 {
    transform: rotate(45deg);
    transition: all 0.5s ease-out;
  }
  .nav-bar.show-on-mobile .burger .line3 {
    transform: rotate(315deg) translate(5px, -6px);
    transition: all 0.5s ease-out;
  }
  .nav-bar.show-on-mobile [class*=line] {
    background-color: #ffffff;
  }
  .nav-bar [class*=line] {
    width: 25px;
    height: 3px;
    background-color: #141720;
    margin: 5px;
  }
}

