@keyframes circle{
    0%{
        border:4px solid grey;
        transform: rotate(-180deg);
    }
    25%{
        border-left: 4px solid grey;
        border-right: 4px solid grey;
        border-top: 4px solid #c71122;
        border-bottom: 4px solid grey;
    }
    50%{
        border-left: 4px solid grey;
        border-right: 4px solid #c71122;
        border-top: 4px solid #c71122;
        border-bottom: 4px solid grey;
    }
    75%{
        border-left: 4px solid grey;
        border-right: 4px solid #c71122;
        border-top: 4px solid #c71122;
        border-bottom: 4px solid #c71122;
    }
    100%{
        border:4px solid #c71122;
        transform: rotate(180deg);
    }
}

@keyframes slide{
    0%{
        transform:translateX(200%);
    }
    10%{
        transform:translateX(0);
    }
    50%{
        transform:translateX(0);
    }
    90%{
        transform:translateX(0);
    }
    100%{
        transform:translateX(200%);
    }
}

@keyframes zoom{  
    to{ 
        transform: scale(1);
    } 
}

[data-src]{
    opacity: 0;
}

.fadein{
    opacity: 1;
    transition: all 1s ease-in;
}